import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

@Injectable()
export class EmployeeLeavePolicyService extends Api {

  private _base_url = '/employees/leavepolicies';

  getEmployeeLeavePolicy(id: string): Promise<any> {

    return this.get(this._base_url + '/' + id)
      .then(response => response);
  }
}
