import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

export class Department {

  _id: string;
  organization_id: string;
  name: string = '';
  count: {
    employees: number;
  };
  designations: string[] = new Array();
  department_status: number;
}

@Injectable()
export class DepartmentService extends Api {

  private _base_url: string = '/organizations/departments';

  getDepartments(query?: Map<string, any>, fields?: Array<string>): Promise<Department[]> {

    if (!query) {
      query = new Map<string, any>();
    }
    if (fields) {
      query.set('fields', fields.join(','));
    }

    return this.get(this._base_url, query)
      .then(response => response['data'] as Department[]);
  }

  getDepartment(id: string): Promise<Department> {

    return this.get(this._base_url + '/' + id)
      .then(response => response as Department);
  }

  postDepartment(department: Department) {

    return this.post(this._base_url, department)
      .then(response => response['department_id'] as string);
  }

  putDepartment(department: Department) {

    return this.put(this._base_url + '/' + department._id.toString(), {
      name: department.name,
      designations: department.designations
    }).then(response => response);
  }
}
