import { Directive, Attribute } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, Validator, ValidationErrors } from '@angular/forms';

@Directive({
  selector: '[app-maxitems][ngModel]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: MaxItemsValidator,
      multi: true
    }
  ]
})
export class MaxItemsValidator implements Validator {

  constructor(
    @Attribute('app-maxitems') private value: number
  ) {

    this.value = Number.parseInt(this.value.toString());
  }

  public validate(control: AbstractControl): ValidationErrors | null {

    if (Array.isArray(control.value) && control.value.length > this.value) {

      return {
        'app-maxitems': {
          requiredItems: this.value
        }
      }
    }

    return null;
  }
}
