import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';
import { TYPE } from 'config/constants/access-control';
import { ACCESS } from 'config/constants/entity';

export class AttributeDetails {

  attribute: string;
  name: string;
}

export class AttributeListResponse {

  default?: string[] = new Array<string>();
  blocked?: string[] = new Array<string>();
}

export class AttributeResponse {

  model: string = '';
  name?: string = '';
  find?: AttributeListResponse = new AttributeListResponse();
  update?: AttributeListResponse = new AttributeListResponse();
  attributes?: AttributeDetails[];
}

export class AccessControlAttribute {

  model: string;
  find_all?: boolean;
  find: string[];
  update_all?: boolean;
  update: string[];
}

export class Action {

  action_id: string;
  title: string;
  description: string;
  access_type: ACCESS;
}

export class Entity {

  _id: string;
  entity: string;
  description: string;
  entity_type: TYPE;
  is_feature: boolean;
  url: string;
  actions: Action[];
}

export class Module {

  module_code: string;
  module: string;
  entities: Entity[];
}

export class ModuleEntitiesResponse {

  module: string;
  module_code: string;
  entities: Entity[];
};

export class FeatureEntitiesResponse {

  feature: string;
  feature_code: string;
  modules: Module[];
};

export class AccessControlListResponse {

  _id: string;
  title: string = '';
  description: string;
  access_control_type: number;
  access_control_status: number;
}

export class AccessControlUpdate {

  actions: boolean;
  attributes: boolean;
}

export class AccessControl {

  _id: string;
  title: string = '';
  description: string;
  organization_id: string;
  access_control_type: number;
  actions: string[] = new Array<string>();
  attributes: AccessControlAttribute[] = new Array<AccessControlAttribute>();
  access_control_status: number;
  update: AccessControlUpdate;
}

@Injectable()
export class AccessControlService extends Api {

  private _base_url = '/accesscontrols';
  private _entities_base_url = '/entities';

  getEntities(): Promise<ModuleEntitiesResponse[]> {

    return this.get(this._entities_base_url)
      .then(response => {
        return response['data'] as ModuleEntitiesResponse[];
      });
  }

  getAttributes(): Promise<AttributeResponse[]> {

    return this.get(this._entities_base_url + '/attributes')
      .then(response => {
        return response['data'] as AttributeResponse[];
      });
  }

  getFeatureEntities(): Promise<FeatureEntitiesResponse[]> {

    return this.get(this._entities_base_url + '/features')
      .then(response => {
        return response['data'] as FeatureEntitiesResponse[];
      });
  }

  getAccessControls(page, limit): Promise<{ count: number, access_controls: AccessControlListResponse[] }> {

    const query = new Map<string, any>();
    if (page > 0) {
      query.set('page', page);
    }
    if (limit > 0) {
      query.set('limit', limit);
    }

    return this.get(this._base_url, query)
      .then(response => {
        return {
          count: response['count'],
          access_controls: response['data'] as AccessControlListResponse[]
        };
      });
  }

  getAccessControl(id: string): Promise<AccessControl> {

    return this.get(this._base_url + '/' + id)
      .then(response => response as AccessControl);
  }

  postAccessControl(access_control: AccessControl) {

    return this.post(this._base_url, access_control)
      .then(response => response);
  }

  putAccessControl(access_control: AccessControl) {

    const updateData = new AccessControl();
    updateData.title = access_control.title;
    updateData.description = access_control.description;

    if (access_control.update.actions) {
      updateData.actions = access_control.actions;
    } else {
      delete updateData.actions;
    }

    if (access_control.update.attributes) {
      updateData.attributes = access_control.attributes;
    } else {
      delete updateData.attributes;
    }

    return this.put(this._base_url + '/' + access_control._id.toString(), updateData)
      .then(response => response);
  }
}
