import { Directive, Attribute } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, Validator, ValidationErrors } from '@angular/forms';

@Directive({
  selector: '[app-exactmatch][ngModel]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: ExactMatchValidator,
      multi: true
    }
  ]
})
export class ExactMatchValidator implements Validator {

  constructor(
    @Attribute('app-exactmatch') private value: string,
    @Attribute('app-exactmatch-text') private text: string = '',
    @Attribute('app-exactmatch-reverse') private reverse: boolean = false
  ) { }

  public validate(control: AbstractControl): ValidationErrors | null {

    const element = control.root.get(this.value);

    if (element && control.value !== element.value && !this.reverse) {

      return {
        'app-exactmatch': {
          requiredMatch: this.text
        }
      }
    }

    if (element && control.value === element.value && this.reverse) {

      delete element.errors['app-exactmatch'];
      if (!Object.keys(element.errors).length) {
        element.setErrors(null);
      }
    }

    if (element && control.value !== element.value && this.reverse) {
      element.setErrors({
        'app-exactmatch': {
          requiredMatch: this.text
        }
      });
    }

    return null;
  }
}
